import React from "react"
import styled from "styled-components"

const WrapLayout = styled.div`
  border: 1px solid #e2e2e2;
  background: white;
  & .a-header {
    background: white;
    border-bottom: 1px solid #e2e2e2;
    padding: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    & .a-title {
      font-weight: bold;
    }
  }
  & .a-body {
    padding: 1rem;
  }
`
class MyLayoutBase extends React.Component {
  render() {
    return (
      <WrapLayout style={this.props.style} className={this.props.className}>
        <div className="a-header">
          <div>
            <div className="a-title">{this.props.title}</div>
            <div className="a-des">{this.props.des}</div>
          </div>
          <div></div>
        </div>
        <div className="a-body">{this.props.children}</div>
      </WrapLayout>
    )
  }
}
MyLayoutBase.defaultProps = {}
export default MyLayoutBase
